import axios from 'axios'

export default {
	name: 'coin',

	data() {
		return {
			coin: []
		}
	},

	beforeCreate() {
		const self = this
		const coinId = `https://api.coinmarketcap.com/v1/ticker/${self.$route.params.id}`

		axios.get(coinId)
		     .then(response => {
			     this.coin = response.data
		     })
		     .catch(error => {
			     console.log(error)
			     this.data = []
		     })
	}
}



